<script>

    export let input
    export let numLevels
	let level_descriptions = [
		"The organization is aware of the relevance of agility capabilities and their anchoring in the resp. agility dimensions. The goal of enhancing agile maturity has been communicated and is reflected in work practices and collaboration  approaches.",
		"The organization has already taken first steps towards the development of agility capabilities and thus satisfying the goal of enhancing agile maturity. All activities implemented follow this goal and ensure customer centricity and satisfaction.",
		"The organization has  further focused on the realization of agility capabilities. All activities are centered around the customer and creating customer value. The organization uses the value of resources within and outside the organization for deliver high quality products and services.",
		"The organization has focused on agility capabilities as a priority in several dimensions. Thus, the organization can respond to change in an effective and productive manner. The organization focuses on constant improvement and uncertainty management. ",
		"The organization has holistically anchored agility within its strategy, structure, processes, culture, workforces’ mindset, and leadership approach. The org. establishes a constant vibrant environment to sustain agility and to follow an organizational learning approach. "
	]

	let dimension_descriptions = {
		"Strategy & Purpose": "Strategy & Purpose comprises the purpose of an organization, its vision and mission as well as its business model.	",
		"Structure": "Structure comprises the organizational structure, the gover-nance, and the functions and roles within the organization.",
		"Process": "Process comprises the process organization and the information and decision flow within the organization",
		"IT": "Information Technology comprises the techn. infrastructure and architecture and the application of technology.",
		"People": "People & Culture comprises the organizational culture, the behavior and mindset of individual employees, and their skills.",
		"Leadership": "Leadership comprises the leadership approach, leadership values and attitude, and workforce empowerment.",
		"Customer-Centric Product Development": "Customer-Centric Product Development comprises the approach to build products that place the customer at its core."
	}


    function processInput(event) {
		input[this.dataset.dimension][this.name].value = Number(this.value)
		console.log(input[this.dataset.dimension])
	}

</script>

{#each Object.entries(input) as [dimension, capabilities]}
    <div class="capability_container">

        <div class="titlebar">
            <h3 class="leveltooltip">{dimension}
				<span class="leveltooltiptext">{dimension_descriptions[dimension]}</span>
			</h3>
        </div>

        <div class="capabilities inline">
            {#each Object.entries(capabilities) as [capability, content]}
                <span class="capability_title tooltip">{capability} 
					<span class="tooltiptext">{content.description}</span>
				</span>
            {/each}
        </div>
    
        <div class="level_selection inline">
            <div class="level_list">
                {#each Array(numLevels) as _, i}
					<span class="level_title inline leveltooltip">Level {i + 1} 
						<span class="leveltooltiptext">{level_descriptions[i]}</span>
					</span>
					<!-- <div class="level_title inline">Level {i + 1}</div> -->
                {/each}
            </div>
            
            {#each Object.entries(capabilities) as [capability, content]}
                <!-- svelte-ignore a11y-label-has-associated-control -->
                <label class="level_radio">
                    {#each Array(numLevels) as _, i}
						{#if i+1 == input[dimension][capability].value}
                        <input type="radio" name={capability} data-dimension={dimension} value={i+1} checked="checked" on:change={processInput}>
						{:else}
                        <input type="radio" name={capability} data-dimension={dimension} value={i+1} on:change={processInput}>
						{/if}
                    {/each}
                </label>
            {/each}
        </div>
    </div>
{/each}

<style>
    .inline {
		display: inline-block;
	}

	.capabilities {
		width: 30%;
		text-align: right;
	}

	.level_selection {
		width: 40%;
		padding: 10px;
		margin-right: 20%;
	}

	.level_radio {
		height: 50px;
		display: flex;
		justify-content:  space-between;
	}

	.level_radio input {
		margin: auto;
	}

	.capability_title {
		position: relative;
		display: block;
		margin-right: 20px;
		height: 50px;
	}

	.level_list {
		width: 100%;
		display: inline-flex;
	}

	.level_title {
		margin: auto;
	}

	.titlebar h3 {
		margin: 0;
		margin-bottom: 10px;
		margin-top: 30px;
	}

	/* Tooltip text */
	.tooltip .tooltiptext {
		visibility: hidden;
		width: 100%;
		background-color: #555;
		color: #fff;
		text-align: center;
		padding: 5px 0;
		border-radius: 6px;

		/* Position the tooltip text */
		position: absolute;
		z-index: 1;
		bottom: 60px;
		left: 0%;

		/* Fade in tooltip */
		opacity: 0;
		transition: opacity 0.3s;
	}

	/* Tooltip arrow */
	.tooltip .tooltiptext::after {
		content: "";
		position: absolute;
		top: 100%;
		left: 80%;
		margin-left: -5px;
		border-width: 5px;
		border-style: solid;
		border-color: #555 transparent transparent transparent;
	}

	/* Show the tooltip text when you mouse over the tooltip container */
	.tooltip:hover .tooltiptext {
		visibility: visible;
		opacity: 1;
	}

	/* The same but for levels */

	/* Tooltip text */
	.leveltooltip .leveltooltiptext {
		visibility: hidden;
		width: 100%;
		background-color: #555;
		color: #fff;
		text-align: center;
		padding: 5px 0;
		border-radius: 6px;

		/* Position the tooltip text */
		position: fixed;
		z-index: 1;
		top: 10%;
		left: 0%;

		/* Fade in tooltip */
		opacity: 0;
		transition: opacity 0.3s;

		font-weight: normal;
	}

	/* Tooltip arrow */
/* 	.leveltooltip .leveltooltiptext::after {
		content: "";
		position: absolute;
		top: 100%;
		left: 80%;
		margin-left: -5px;
		border-width: 5px;
		border-style: solid;
		border-color: #555 transparent transparent transparent;
	} */

	/* Show the tooltip text when you mouse over the tooltip container */
	.leveltooltip:hover .leveltooltiptext {
		visibility: visible;
		opacity: 1;
	}

</style>